// 放入購物車後動畫延遲2.5s
// title 0.8s
// 前一跑完後接續延遲0.2s

* {
    animation-play-state: paused;
}

.-loaded * {
    animation-play-state: running;
}

.-isPause * {
    animation-play-state: paused !important;
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@keyframes cartShake {
    0% {
        transform: scaleY(1);
    }

    50% {
        transform: scaleY(0.99);
    }

    100% {
        transform: scaleY(1);
    }
}

@keyframes cartIn {
    0% {
        transform: translate(-130%, 0);
    }

    100% {
        transform: translate(0, 0);
    }
}

@keyframes cartOut {
    0% {
        transform: translate(0, 0);
        opacity: 1;
    }

    99.9% {
        transform: translate(140%, 0);
        opacity: 1;
    }

    100% {
        transform: translate(140%, 0);
        opacity: 0;
    }
}

@keyframes zoomIn {
    0% {
        transform: scale(0.5);
        opacity: 0;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes itemShake {
    0% {
        transform: rotate(-3deg);
    }

    100% {
        transform: rotate(3deg);
    }
}

@keyframes lgItemShake {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(3deg);
    }
}

