@keyframes throwL {
    0% {
        transform: translate(-60%, -150%);
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    90% {
        transform: translate(0, 0);
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

@keyframes throwM {
    0% {
        transform: translate(-20%, -150%);
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    90% {
        transform: translate(0, 0);
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

@keyframes throwR {
    0% {
        transform: translate(50%, -150%);
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    90% {
        transform: translate(0, 0);
        opacity: 1;
