.cart {

    &-item {
        opacity: 0;
    }

    &-empty,
    &-result {
        img {
            transform-origin: bottom center;
            animation: cartShake 1s infinite;
        }
    }

    .-cartIn & {
        &-empty {
            transform: translate(-130%, 50%);
        }
    }

    .container-item:not(.-cartIn) & {
        &-result {
            opacity: 1;
        }
    }

    .swiper-slide-active.-cartIn & {
        &-item {
            animation: 1s ease-in-out 1.5s forwards;

            img {
                animation: resultIn .2s 2.5s reverse forwards;
            }

            &.-cabbage {
                animation-name: throwM;
            }

            &.-milk {
                animation-name: throwL;
            }

            &.-jam {
                animation-name: throwR;
            }

            &.-lemon {
                animation-name: throwL;
            }

            &.-orange {
                animation-name: throwM;
            }

            &.-tomato {
                animation-name: throwR;
            }

            &.-jam2 {
                animation-name: throwR;
            }

            &.-bread {
                animation-name: throwL;
            }

            &.-bee {
                animation-name: throwM;
            }
        }

        &-empty {
            animation: 1.5s cubic-bezier(0, 0, .56, 1.38) forwards;
            animation-name: cartIn;
        }
    }

    .swiper-slide-active.-cartIn:not(.-cartOut) & {
        &-result {
            animation: resultIn .3s 2.5s forwards;
        }
    }

    .swiper-slide-active.-cartOut.-fadeOut & {

        &-empty,
        &-result {
            animation: cartOut 1.5s cubic-bezier(.61, -0.4, 1, 1) forwards;
        }
    }
}

