.index {
    &-item {
        animation: itemShake .8s ease-in-out infinite alternate;

        &.-lemon,
        &.-jam,
        &.-shoppingBag {
            animation-direction: alternate-reverse;
        }

        &.-bag,
        &.-shoppingBag {
            animation-name: lgItemShake;
        }
    }

    &-title img {
        opacity: 0;
    }

    &-cart>div {
        transform: translate(-150%, 50%);
    }

    .swiper-slide-active & {
        &-title img {
            animation: zoomIn 1s ease-out 1.2s forwards;
        }

        &-cart>div {
            animation: 1.5s cubic-bezier(0, 0, .56, 1.38) forwards;
            animation-name: cartIn;

            img {
                transform-origin: bottom center;
                animation: cartShake 1s infinite;
            }
        }
    }

    .swiper-slide-active.-fadeOut & {
        &-cart>div {
            animation: cartOut 1.5s cubic-bezier(.61, -0.4, 1, 1) forwards;
        }
    }

    .-changeStart & {
        &-wrap {
            opacity: 0 !important;
        }
    }
}

