    }
}

.newYear {
    transform: translateX(100%);

    .swiper-slide-active & {
        animation: newYearMove 5.6s linear;
    }
}

.important {
    position: relative;
    height: 100%;
    background: url(../image/bg/bg08.jpg) no-repeat center/cover;

    &-memo {
        position: absolute;
        top: 47.5%;
        left: 51%;
        transform: translate(-50%, -50%);
        width: percent(1080);
        height: percent(1866);

        &>div {
            width: 100%;
            height: 100%;
            background: url(../image/bg/memo.png) no-repeat center/contain;
        }
    }

    &-content {
        padding: 44% 0 16% 0;
    }

    &-text {
        b {
            line-height: 1.3;

            &.-lg {
                line-height: 1;
            }
        }

        .text+.text {
            margin-top: 3%;
        }
    }

    &-btn {
        width: percent(684);
        margin: 5% auto;
    }

    &-note {
        margin: 2% auto 0;
        font-size: percent(36);
        font-weight: 500;
        color: #764e42;
        text-align: center;
    }

    &-cart {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: percent(830);
    }

    &-again {
        margin-top: 20%;
        text-align: center;

        img {
            width: percent(513);
        }
    }

    @include breakpoint-up('tablet') {
        &-memo {
            width: percentVh(1080);
            height: percentVh(1866);
        }

        &-btn {
            width: percentVh(684);
        }

        &-note {
            font-size: percentVh(36);
        }

        &-cart {
            width: percentVh(830);
        }

        &-again {
            img {
                width: percentVh(513);
            }
        }
    }
}

.important {

    &-memo>div,
    &-text,
    &-btn,
    &-again {
        opacity: 0;
    }

    &-btn {
        img {
            animation: lgItemShake .5s ease-in-out infinite alternate;
        }
    }

    &-cart>div {
        transform: translate(-130%, 0);
    }

    .swiper-slide-active.-cartIn & {
        &-memo {
            &>div {
                transform-origin: top left;
                animation: polaroid 1s ease-out forwards;
            }
        }

        &-text {
            &.-an1 {
                animation: zoomIn .8s ease-out 1.7s forwards;

                b {
                    animation: textJump .5s cubic-bezier(0, 0, .56, 1.38) 1.7s forwards;
                }
            }

            &.-an2 {
                animation: zoomIn .8s ease-out 3.7s forwards;

                b {
                    animation: textJump .5s cubic-bezier(0, 0, .56, 1.38) 3.7s forwards;
                }
            }
        }

        &-btn {
            animation: zoomIn .8s ease-out 2.7s forwards;
        }

        &-again {
            animation: zoomIn .8s ease-out 3.7s forwards;
        }

        &-cart>div {
            animation: 1.5s cubic-bezier(0, 0, .56, 1.38) forwards;
            animation-name: cartIn;

            img {
                transform-origin: bottom center;
                animation: cartShake 1s infinite;
            }
        }
    }
}