@import "./_variables";
@import "./_mixins";

html,
body {
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
}

.container {
    position: relative;
    max-width: 542px;
    height: 100%;
    min-height: 167vw;
    margin: 0 auto;
    user-select: none;
    overflow: hidden;

    &-item {
        position: relative;
        overflow: hidden;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: url(../image/logo-wt.png) no-repeat top 4% left 4%/percent(140);
        }
    }

    & &-page {
        box-sizing: border-box;
        top: percent(50);
        bottom: auto;
        display: flex;
        padding: 0 percent(50);
        line-height: 0;

        .swiper-pagination-bullet {
            position: relative;
            flex: 1;
            box-sizing: border-box;
            width: auto;
            height: percent(8);
            margin: 0;
            box-shadow: 0 0 percent(40) 0 rgba($color: #000000, $alpha: 0.2);
            border-radius: percent(8);
            background-color: rgba($color: #000000, $alpha: 0.3);
            opacity: 1;
            overflow: hidden;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border-radius: percent(8);
                background-color: #FFF;
            }

            &+.swiper-pagination-bullet {
                margin-left: percent(5);
            }

            &.swiper-pagination-bullet-active {
                &::before {
                    width: calc(var(--progress)*100%);
                }

                &~.swiper-pagination-bullet {
                    &::before {
                        width: 0;
                    }
                }
            }
        }
    }

    &-prev,
    &-next {
        top: 0;
        width: 50%;
        height: 100%;
        margin-top: 0;
        outline: none;

        &::after {
            content: none;
        }

        .-isPause & {
            pointer-events: none;
        }
    }

    &-prev {
        left: 0;
    }

    &-next {
        right: 0;
    }

    &-btn {
        position: absolute;
        bottom: position(720);
        left: percent(230);
        width: percent(742);
        height: percent(241);
        z-index: 100;
        cursor: pointer;

        &.-vip {
            height: percent(416);
            bottom: position(665);
        }
    }

    &-goShop {
        position: absolute;
        top: 47.5%;
        left: 51%;
        transform: translate(-50%, -50%);
        width: percent(1033);
        height: percent(1785);
        pointer-events: none;
        z-index: 100;

        a.goShop {
            position: absolute;
            bottom: 53%;
            left: percent(150);
            display: block;
            width: percent(742);
            height: percent(241);
            cursor: pointer;
            pointer-events: visible;
            // background: rgba($color: #000000, $alpha: 0.2);
        }

        a.again {
            position: absolute;
            bottom: 23.5%;
            left: percent(257);
            display: block;
            width: percent(513);
            height: percent(150);
            cursor: pointer;
            pointer-events: visible;
            // background: rgba($color: #000000, $alpha: 0.2);
        }
    }

    @include breakpoint-up('tablet') {
        max-width: 59.877vh;
        min-height: auto;

        &-item {
            &::after {
                background-size: percentVh(140);
            }
        }

        & &-page {
            top: percentVh(50);
            padding: 0 percentVh(50);

            .swiper-pagination-bullet {
                height: percentVh(8);
                box-shadow: 0 0 percentVh(40) 0 rgba($color: #000000, $alpha: 0.2);
                border-radius: percentVh(8);

                &::before {
                    border-radius: percentVh(8);
                }

                &+.swiper-pagination-bullet {
                    margin-left: percentVh(5);
                }
            }
        }

        &-btn {
            left: percentVh(230);
            width: percentVh(742);
            height: percentVh(241);

            &.-vip {
                height: percentVh(416);
            }
        }

        &-goShop {
            width: percentVh(1033);
            height: percentVh(1785);

            a.goShop {
                left: percentVh(150);
                width: percentVh(742);
                height: percentVh(241);
            }

            a.again {
                left: percentVh(257);
                width: percentVh(513);
                height: percentVh(150);
            }
        }
    }
}