.authentication {
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
    background: url(../image/bg/bg08.jpg) no-repeat center/cover;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        pointer-events: none;
        background: url(../image/logo-wt.png) no-repeat top 4% left 4%/percent(140);
    }

    &-wrap {
        flex: 0 0 auto;
        width: 100%;
        text-align: center;
    }

    &-title {
        width: percent(775);
        margin: 0 auto percent(140);
    }

    &-text {
        margin-bottom: percent(24);
        padding-left: percent(5);
        font-size: percent(54);
        color: #fff;
        letter-spacing: percent(5);
    }

    &-input {
        box-sizing: border-box;
        display: block;
        width: percent(678);
        height: percent(140);
        margin: 0 auto;
        padding: 0 0 0 percent(10);
        font-size: percent(80);
        color: #fff;
        letter-spacing: percent(10);
        text-align: center;
        background-color: transparent;
        border: none;
        border-bottom: percent(6) solid #fff;
        border-radius: 0;
        outline: none;

        &::-webkit-inner-spin-button,
        &::-webkit-outer-spin-button {
            -webkit-appearance: none;
        }

        &:focus {
            outline: none;
        }
    }

    &-btn {
        box-sizing: border-box;
        display: block;
        margin: percent(150) auto 0;
        padding: percent(24);
        width: percent(483);
        height: percent(132);
        font-size: percent(54);
        color: #fff;
        text-decoration: none;
        border-radius: percent(10);
        background-color: #c48475;
    }

    @include breakpoint-up('tablet') {
        &::after {
            background-size: percentVh(140);
        }

        &-title {
            width: percentVh(775);
            margin: 0 auto percentVh(140);
        }

        &-text {
            margin-bottom: percentVh(24);
            padding-left: percentVh(5);
            font-size: percentVh(54);
            letter-spacing: percentVh(5);
        }

        &-input {
            width: percentVh(678);
            height: percentVh(140);
            padding: 0 0 0 percentVh(10);
            font-size: percentVh(80);
            letter-spacing: percentVh(10);
            border-bottom: percentVh(6) solid #fff;
        }

        &-btn {
            margin: percentVh(150) auto 0;
            padding: percentVh(24);
            width: percentVh(483);
            height: percentVh(132);
            font-size: percentVh(54);
            border-radius: percentVh(10);
        }
    }
}