h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-size: 1em;
}

p {
    margin: 0;
}

.-rdT {
    color: #cb0000;
}

.-gnT {
    color: #005422;
}

@keyframes textJump {
    0% {
        transform: scaleY(0);
    }

    100% {
        transform: scaleY(1);
    }
}

.text {
    font-size: percent(60);
    font-weight: bold;
    text-align: center;
    color: #764e42;

    span {
        display: inline-flex;
        align-items: flex-end;
    }

    b {
        font-size: percent(80);
        transform: scaleY(0);
        transform-origin: bottom center;

        &.-lg {
            font-size: percent(100);
        }

        &.-sm {
            font-size: percent(60);
        }
    }

    img {
        width: auto;
        height: percent(114);
        vertical-align: bottom;
    }

    &.-pk {
        font-size: percent(64);
        color: #e87278;
    }

    &+& {
        margin-top: 6%;
    }

    @include breakpoint-up('tablet') {
        font-size: percentVh(60);

        img {
            height: percentVh(114);
        }

        &.-pk {
            font-size: percentVh(64);
        }

        b {
            font-size: percentVh(80);

            &.-lg {
                font-size: percentVh(100);
            }

            &.-sm {
                font-size: percentVh(60);
            }
        }
    }
}

.-note {
    font-size: 0.6em;
}