.end {
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
    background: url(../image/bg/bg08.jpg) no-repeat center/cover;
    text-align: center;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        pointer-events: none;
        background: url(../image/logo-wt.png) no-repeat top 4% left 4%/percent(140);
    }

    &-text {
        width: 100%;
        margin-bottom: percent(60);
        padding-left: percent(5);
        font-size: percent(54);
        color: #fff;
        letter-spacing: percent(5);
    }

    @include breakpoint-up('tablet') {
        &::after {
            background-size: percentVh(140);
        }

        &-text {
            margin-bottom: percentVh(60);
            padding-left: percentVh(5);
            font-size: percentVh(54);
            letter-spacing: percentVh(5);
        }
    }
}