@keyframes loading {
    0% {
        transform: translate(0, 0);
    }

    50% {
        transform: translate(0, 80%);
    }

    100% {
        transform: translate(0, 0);
    }
}

.loading {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 9999;

    &-wrap {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center;
    }

    &-dot {
        display: inline-block;
        width: percent(45);
        height: percent(45);
        margin: 0 percent(10);
        border-radius: 50%;
        background-color: #305ABF;
        animation: loading 0.6s linear infinite;

        &:nth-child(1) {
            animation-delay: .1s;
        }

        &:nth-child(2) {
            animation-delay: .2s;
        }

        &:nth-child(3) {
            animation-delay: .3s;
        }
    }

    &-text {
        margin-top: percent(90);
        padding-left: 1em;
        font-size: percent(45);
    }

    @include breakpoint-up('tablet') {
        &-dot {
            width: percentVh(45);
            height: percentVh(45);
            margin: 0 percentVh(10);
        }

        &-text {
            margin-top: percentVh(90);
            font-size: percentVh(45);
        }
    }
}